@use '../base/variables' as v;
@use '../base/mixins' as m;
.sitios{
    display: flex;
    flex-direction: column;
    margin-top: 20px;

    //titulos de la pagina de sitios
    .titulo-html-css{
        text-align: center;
        margin-top: 10px;
    }
    .titulo-fullstack{
        text-align: center;
        .descripcion-fullstack{
            color: v.$cian;
        }
    }
    .titulo-jsmoderno{
        text-align: center;
    }

    .galeria-sitios{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        justify-items: center;
        margin-top: 10px;
        
        
         .imagen{
            margin: 10px 0 10px 0;
             width: 320px;
             height: 200px;
         }
         
     }
     //contenedor de la imagen de viajes
    .proyectos-fullstack{
    
    max-width: 800px;
    margin: 0 auto;

    .proyecto-viaje{
        width: 100%;

        margin: 1rem auto;
        
    }
    }
}
@include m.tablet{
    .sitios{
        .galeria-sitios{
            grid-template-columns: repeat(2, 1fr);
           
        }
    }
}
@include m.celular{
    .sitios{
        .galeria-sitios{
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }
}