@use '../base/variables' as v;
@use '../base/mixins' as m;
.footer{
    display: flex;
    flex-direction: column;
    min-height: 3.4rem;
    width: 100%;
    background-color: v.$grisTansparente;
    text-align: center;
    color: v.$blanco;
    padding: 1rem;
    .contactos{
        display: flex;
        flex-direction: column;
        margin-top: 10px;


    }
    .contactos:hover{
        color: v.$grisTansparente;
    }
}