@use '../base/variables' as v;
@use '../base/mixins' as m;

.seccion1{
    display: flex;
    flex-direction: row;
    gap: 2rem;
    align-items: center;
    text-transform: uppercase;
    text-shadow: 0px 6px 8px rgba(0, 0, 0, 0.3);
    margin: 10px 0 0 25px;

.galeria{
    .contenido-galeria{
        
       
        max-width: 600px;
        height: 300px;
        
        
        z-index: 10;
        box-shadow: 0px 0px 36px -2px rgba(0,0,0,0.75);
        margin: 0 0;
        
        img{
            height: 100%;
            width: 600px;
            
        }
        
    
       /* @include m.celular{
            width: 300px;
            height: 250;
            .imagen{
                width: 100%;
                height: 100%;
            }
            
    
        }*/
        
    }
    section{
        margin: 0 0 0 0;
        display: flex;
        flex-direction: column;
        gap: .8rem;
    }

    ///Botones
    .botones-galeria{
        display: flex;
        justify-content: space-around;
        margin-top: 15px;
        .btn {
            padding: 0.8em 1em;
            border: none;
            outline: none;
            color: #FFF;
            
            font-weight: bold;
            font-size: 17px;
            cursor: pointer;
            position: relative;
            z-index: 0;
            border-radius: 12px;
           
            
        }
        .btn-shadow{
            box-shadow: 2px 10px 32px -10px #000000d4;
        }
    }

    .btn::after {
        content: "";
        z-index: -1;
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: rgb(46, 46, 46);
        left: 0;
        top: 0;
        border-radius: 10px;
       }
       .btn::before {
        content: "";
        background: linear-gradient(
           45deg,
           #FF0000, #002BFF, #FF00C8, #002BFF,
             #FF0000, #002BFF, #FF00C8, #002BFF
       );
        position: absolute;
        top: -2px;
        left: -2px;
        background-size: 600%;
        z-index: -1;
        width: calc(100% + 4px);
        height: calc(100% + 4px);
        filter: blur(8px);
        animation: glowing 20s linear infinite;
        transition: opacity .3s ease-in-out;
        border-radius: 10px;
        opacity: 0;
       }
       .btn:hover::before {
        opacity: 1;
       }
       
       .btn:active:after {
        background: transparent;
       }
       
       .btn:active {
        color: #000;
        font-weight: bold;
       }

    

}
