@use '../base/variables' as v;
@use '../base/mixins' as m;
*{
   
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-family: v.$fuente_principal;

 }

.header{
    overflow: hidden;
    height: 25rem;
    
    .nav-principal{
        
        display: flex;
        gap: 2px;
        justify-content: space-between;
        align-items: center;
        height: 3.8rem;
        width: 100%;
        background-color: v.$grisTansparente;
        position: fixed;
        z-index: 999;
       
        .nav-btn{
            margin: 0 10px 0 10px;
            padding: 2% 0 2% 0;
            background-color: rgba(32, 35, 37, 0);
            cursor: pointer;
            border: none;
            height: 100%;
            width: 10rem;
            text-align: center;
            font-weight: bold;
            box-shadow: 0px 6px 55px 39px rgba(16, 16, 16, 0.018) inset;
            text-transform: uppercase;
            z-index: 999;
        }
        .titulo-portafolio{
            font-family: 'Manifesto';
            font-size: 1.5rem;
            text-align: center;
            
            text-transform: uppercase;
            
            width: 20rem;
        }
    
    }
    
   /* .nav-btn{
        background-color: rgba(32, 35, 37, 0.413);
        cursor: pointer;
        border: none;
        height: 40px;
        margin: 0 10px 0 10px;
        padding: 0 10px;
        font-weight: bold;
        box-shadow: 0px 6px 55px 39px rgba(16, 16, 16, 0.018) inset;
        text-transform: uppercase;
        z-index: 999;
        
    
    }*/
    
    .nav-principal > a{
        color: v.$grisClaro;
        background-color: none;
        a{
            color: v.$grisClaro;
        }
        
    }
    .nav-principal > a:hover{
        
        color: v.$blanco;
        box-shadow: -1px 2px 19px 0px rgb(0, 0, 0);
        
    }
    
    div.contenedor-icono{
        margin: 65px 20px 0 auto;
        height: 90px;
        width: 90px;
        filter: invert(90%);
        
        .icono{
            background-position: center center;
            background-size: cover;
            width: 100%;          
            animation: dropanimacionColor 4s infinite;
            
        }
    }

    
    .contenido-header{
        /*display: grid;
        grid-template-areas: 
            "navegacion navegacion icono"
                "titulo titulo titulo"
            
        ;*/
        display: flex;
        width: 100%;
        height: 60%;

        .nav-principal{
            grid-area: navegacion;
        }
        .icono{
           
            grid-area: icono;
        }
        .titulo-galeria{
            grid-area: titulo;
        }
        
    }

    .titulo{
        color: v.$blanco;
        font-size: 1.1rem;
        
    }
    .titulo-header{
        display: inline-block;
        position: relative;
        top: -170px;
        padding: 0 0 0 1rem;
        
       
    }
    .titulo-galeria{
        text-shadow: 0px 6px 8px rgba(0,0,0,0.6);

    }
    
    
    
    
    /*.two-row-mobile{
        display: grid;
        grid-template-rows: repeat(2, 1fr) !important;
    }*/
    .container{
        display: flex;
        
        align-items: center;
        justify-content: center;
        gap: 15px;
        text-transform: uppercase;
        //background-color: red;
        height: 11rem;
        width: 550px;
        font-size: 1.8rem;
        font-weight: bold;
        .flexb{
            display: flex;
        }
        p{
            animation: glitch 650ms infinite;
            .emoticon{
                animation: none !important;
            }
        }
        
        .animation{
            height: 30px;
            overflow: hidden;
            text-align: center; 
            

        }
        .animation{
            div:first-child{

                animation: texto-header 8s infinite;
            }
        }

        .first {
            background-color: #20a7d8;
            animation: glitch 500ms infinite;
        }
        .second{
            background-color: #CD921E;
        }
        .third{
            background-color: #c10528;
        }
        .four{
            background-color: #2cd601;
        }
        
    }
    
    


}
@include m.desktop{
    .titulo-header{
        top: -270px;
        font-size: 2.6rem;
    }
    .list-nav{
        display: flex;
        flex-direction: column;
        justify-content: start;
    }
}


@include m.tablet{
    
    .titulo-header{
        top: -270px;
        font-size: 2.6rem;
    }
    .list-nav{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        
    }
    
    
    
}
@include m.celular{
    
    .titulo-header{
        top: -190px;
        font-size: 1.5rem;
        top: -180px;
        

        
    }
   
    .titulo-galeria{
        font-size: 2.1rem;
    }
    .list-nav{
        display: flex;
        flex-direction: column;
        justify-content: start;
    }
    .contenedor-icono{
        margin: 0;
        position: relative;
        top: 110px;
        right: 55px;
        
    }
    
}

.inicio{
   
    background-position: center center;
    background-image: url(../img/header.png);
    
    background-size: cover;
    
    @include m.tablet{
    }
    
}

