@use '../base/variables' as v;
@use '../base/mixins' as m;
.skills{
    display: grid;

    grid-template-rows: repeat(2,1fr);
    

    
    #frontend{
        display: grid;
        grid-template-columns: repeat(4,1fr);
        width: 100%;
        justify-items: center;
        align-items: center;
    }
    #backend{
        display: grid;
        grid-template-columns: repeat(4,1fr);
        width: 100%;
        justify-items: center;
        align-items: center;

    }
    .container{

            
            height: 11.3rem;
            width: 9.6rem;
            border-radius: 10px 3px 10px 3px;
            transition: all 300ms;
        
            position: relative;   
            top: 2px;
            left: 2px;
            margin-top: 10px;
            box-shadow: 2px 10px 32px -10px #000000d4;
        
            
        .tarjetas-skills{
            background-color: v.$blackClaro;
            height: 11rem;
            width: 9.3rem;
            border-radius: 10px 3px 10px 3px;
            position: absolute;
            left: 2px;
            top: 2px;
            
            display: grid;
            justify-content: center;
            

        
            
        
            .container-circle{
                
                margin-top: .3rem;    
                width: 100px;
                height: 100px;
               
                
                    svg{
                        //position: absolute;
                        transform: rotate(85deg) !important;
                        width: 100px;
                        height: 100px;
                        circle{
                            fill: none;
                        //position: relative;
                            width: 100%;
                            height: 100%;
                    
                            margin: auto;
                            padding: 0;
                            top: 3px;
                            border-radius: 50%;
                            color: #fff;
                            stroke:  #79627583;
                            stroke-width: 10;
                            stroke-linecap: round !important;
                            transform: translate(5px 5px);
                            p{
                                position: absolute;
                                top: 36px;
                                left: 28px;
                            }
                        }
                        circle:nth-child(2){
                            stroke-dasharray: 440;
                            stroke-dashoffset: 440;
                        }

                

                    }
                    p{
                        position: relative;
                        bottom: 63px;
                        left: 40px;
                    }
                    h1{
                        position: relative;
                        bottom: 40px;
                        left: 22px;
                        font-size: 1.5rem;
                    }
                    h2{
                        position: relative;
                        bottom: 50px;
                        left: 10px;
                        font-size: 1.2rem;
                    }


            }
            .html{
                
                svg{
                    circle:nth-child(2){
                        stroke-dashoffset: calc(440 - (440 * 50)/100);
                        stroke: #ff00d0;
                    }
                }
            }
            .css{
            
                svg{
                    circle:nth-child(2){
                        stroke-dashoffset: calc(440 - (440 * 49)/100);
                        stroke: #ff00d0;
                    }
                }
            }
            .js{
            
                svg{
                    circle:nth-child(2){
                        stroke-dashoffset: calc(440 - (440 * 50)/100);
                        stroke: #ff00d0;
                    }
                }
            }

            .react{

                svg{
                    circle:nth-child(2){
                        stroke-dashoffset: calc(440 - (440 * 30)/100);
                        stroke: #ff00d0;
                    }
                }
            }
            .python{

                svg{
                    circle:nth-child(2){
                        stroke-dashoffset: calc(440 - (440 * 45)/100);
                        stroke: #ff00d0;
                    }
                }
                h1{
                    left: 12px;
                 }
                 h2{
                     left: 5px;
                 }
            }
            .node{

                svg{
                    circle:nth-child(2){
                        stroke-dashoffset: calc(440 - (440 * 48)/100);
                        stroke: #ff00d0;
                    }
                }
                h1{
                   left: 12px;
                }
                h2{
                    left: 5px;
                }
            }
            .php{

                svg{
                    circle:nth-child(2){
                        stroke-dashoffset: calc(440 - (440 * 30)/100);
                        stroke: #ff00d0;
                    }
                }
                h1{
                    left: 30px;
                 }
                 h2{
                     left: 5px;
                 }
            }
                
        }
        .tarjetas-skills-w{
            background-color: #fff !important;
            box-shadow: 2px 10px 32px -10px #000000d4;
        }
    }

    
        
        
        
   
  
    //padding: .5rem .2rem 0 .2rem
   .container:hover{
    transform: scale(1.1);
    .rotar{
        height: 230px;
        width: 30px;
        border-radius: 2px;
        background-color: rgb(0, 228, 0);
    
        position: absolute;
        top: -30px;
        left: 62px;
        animation: rotacion 3s ease-out infinite;
        z-index: 0;
    

    }
    
   }
   .container-w{
    box-shadow: 2px 10px 32px -10px #000000d4;
   }


}