* {
  text-decoration: none;
  box-sizing: border-box;
}

body {
  text-decoration: none;
}

img {
  object-fit: cover;
}

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: "Fira Sans", sans-serif;
}

.header {
  overflow: hidden;
  height: 25rem;
  /* .nav-btn{
       background-color: rgba(32, 35, 37, 0.413);
       cursor: pointer;
       border: none;
       height: 40px;
       margin: 0 10px 0 10px;
       padding: 0 10px;
       font-weight: bold;
       box-shadow: 0px 6px 55px 39px rgba(16, 16, 16, 0.018) inset;
       text-transform: uppercase;
       z-index: 999;


   }*/
  /*.two-row-mobile{
      display: grid;
      grid-template-rows: repeat(2, 1fr) !important;
  }*/
}
.header .nav-principal {
  display: flex;
  gap: 2px;
  justify-content: space-between;
  align-items: center;
  height: 3.8rem;
  width: 100%;
  background-color: rgba(32, 35, 37, 0.919);
  position: fixed;
  z-index: 999;
}
.header .nav-principal .nav-btn {
  margin: 0 10px 0 10px;
  padding: 2% 0 2% 0;
  background-color: rgba(32, 35, 37, 0);
  cursor: pointer;
  border: none;
  height: 100%;
  width: 10rem;
  text-align: center;
  font-weight: bold;
  box-shadow: 0px 6px 55px 39px rgba(16, 16, 16, 0.018) inset;
  text-transform: uppercase;
  z-index: 999;
}
.header .nav-principal .titulo-portafolio {
  font-family: "Manifesto";
  font-size: 1.5rem;
  text-align: center;
  text-transform: uppercase;
  width: 20rem;
}
.header .nav-principal > a {
  color: #A6A2A2;
  background-color: none;
}
.header .nav-principal > a a {
  color: #A6A2A2;
}
.header .nav-principal > a:hover {
  color: rgb(241, 241, 241);
  box-shadow: -1px 2px 19px 0px rgb(0, 0, 0);
}
.header div.contenedor-icono {
  margin: 65px 20px 0 auto;
  height: 90px;
  width: 90px;
  filter: invert(90%);
}
.header div.contenedor-icono .icono {
  background-position: center center;
  background-size: cover;
  width: 100%;
  animation: dropanimacionColor 4s infinite;
}
.header .contenido-header {
  /*display: grid;
  grid-template-areas: 
      "navegacion navegacion icono"
          "titulo titulo titulo"

  ;*/
  display: flex;
  width: 100%;
  height: 60%;
}
.header .contenido-header .nav-principal {
  grid-area: navegacion;
}
.header .contenido-header .icono {
  grid-area: icono;
}
.header .contenido-header .titulo-galeria {
  grid-area: titulo;
}
.header .titulo {
  color: rgb(241, 241, 241);
  font-size: 1.1rem;
}
.header .titulo-header {
  display: inline-block;
  position: relative;
  top: -170px;
  padding: 0 0 0 1rem;
}
.header .titulo-galeria {
  text-shadow: 0px 6px 8px rgba(0, 0, 0, 0.6);
}
.header .container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
  text-transform: uppercase;
  height: 11rem;
  width: 550px;
  font-size: 1.8rem;
  font-weight: bold;
}
.header .container .flexb {
  display: flex;
}
.header .container p {
  animation: glitch 650ms infinite;
}
.header .container p .emoticon {
  animation: none !important;
}
.header .container .animation {
  height: 30px;
  overflow: hidden;
  text-align: center;
}
.header .container .animation div:first-child {
  animation: texto-header 8s infinite;
}
.header .container .first {
  background-color: #20a7d8;
  animation: glitch 500ms infinite;
}
.header .container .second {
  background-color: #CD921E;
}
.header .container .third {
  background-color: #c10528;
}
.header .container .four {
  background-color: #2cd601;
}

@media (max-width: 992px) {
  .titulo-header {
    top: -270px;
    font-size: 2.6rem;
  }
  .list-nav {
    display: flex;
    flex-direction: column;
    justify-content: start;
  }
}
@media (max-width: 768px) {
  .titulo-header {
    top: -270px;
    font-size: 2.6rem;
  }
  .list-nav {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}
@media (max-width: 480px) {
  .titulo-header {
    top: -190px;
    font-size: 1.5rem;
    top: -180px;
  }
  .titulo-galeria {
    font-size: 2.1rem;
  }
  .list-nav {
    display: flex;
    flex-direction: column;
    justify-content: start;
  }
  .contenedor-icono {
    margin: 0;
    position: relative;
    top: 110px;
    right: 55px;
  }
}
.inicio {
  background-position: center center;
  background-image: url(../img/header.png);
  background-size: cover;
}
.seccion1 {
  display: flex;
  flex-direction: row;
  gap: 2rem;
  align-items: center;
  text-transform: uppercase;
  text-shadow: 0px 6px 8px rgba(0, 0, 0, 0.3);
  margin: 10px 0 0 25px;
}
.seccion1 .galeria .contenido-galeria {
  max-width: 600px;
  height: 300px;
  z-index: 10;
  box-shadow: 0px 0px 36px -2px rgba(0, 0, 0, 0.75);
  margin: 0 0;
  /* @include m.celular{
       width: 300px;
       height: 250;
       .imagen{
           width: 100%;
           height: 100%;
       }


   }*/
}
.seccion1 .galeria .contenido-galeria img {
  height: 100%;
  width: 600px;
}
.seccion1 .galeria section {
  margin: 0 0 0 0;
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}
.seccion1 .galeria .botones-galeria {
  display: flex;
  justify-content: space-around;
  margin-top: 15px;
}
.seccion1 .galeria .botones-galeria .btn {
  padding: 0.8em 1em;
  border: none;
  outline: none;
  color: #FFF;
  font-weight: bold;
  font-size: 17px;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 12px;
}
.seccion1 .galeria .botones-galeria .btn-shadow {
  box-shadow: 2px 10px 32px -10px rgba(0, 0, 0, 0.831372549);
}
.seccion1 .galeria .btn::after {
  content: "";
  z-index: -1;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgb(46, 46, 46);
  left: 0;
  top: 0;
  border-radius: 10px;
}
.seccion1 .galeria .btn::before {
  content: "";
  background: linear-gradient(45deg, #FF0000, #002BFF, #FF00C8, #002BFF, #FF0000, #002BFF, #FF00C8, #002BFF);
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 600%;
  z-index: -1;
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  filter: blur(8px);
  animation: glowing 20s linear infinite;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
  opacity: 0;
}
.seccion1 .galeria .btn:hover::before {
  opacity: 1;
}
.seccion1 .galeria .btn:active:after {
  background: transparent;
}
.seccion1 .galeria .btn:active {
  color: #000;
  font-weight: bold;
}
.seccion1 .informacion {
  display: grid;
  justify-items: center;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  max-width: 500px;
  height: 400px;
  margin-top: 30px;
}
.seccion1 .informacion .descripcion {
  display: flex;
  align-items: start;
  text-shadow: none;
  font-weight: bold;
}
.seccion1 .informacion .tarjeta-white {
  background-color: #FFF !important;
}
.seccion1 .informacion .tarjeta-white .iconos-tarjeta {
  background-color: #FFF !important;
}
.seccion1 .informacion .tarjeta {
  display: flex;
  flex-direction: column;
  align-items: center;
  filter: drop-shadow(-2px 8px 15px rgba(0, 0, 0, 0.65));
  background-color: rgba(32, 35, 37, 0.494);
  height: 11rem;
  width: 9.3rem;
  border-radius: 5px 5px 0 0;
  transition: all 300ms;
}
