.seccion1 .informacion .tarjeta .iconos-tarjeta {
  width: 100%;
  background-color: rgba(32, 35, 37, 0.494);
  padding: 0.5rem 0.4rem 0 0.4rem;
  border-radius: 0 0 5px 5px;
}
.seccion1 .informacion .tarjeta .iconos-tarjeta .nombre-tarjeta {
  display: none;
}
.seccion1 .informacion .tarjeta .iconos-tarjeta .contenedor-iconos {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-items: center;
}
.seccion1 .informacion .tarjeta .iconos-tarjeta .contenedor-iconos #iconos-tarjeta {
  width: 25px;
  height: 25px;
}
.seccion1 .informacion .tarjeta:hover {
  /*scale: 1.1;*/
  height: 12.7rem;
  transform: scale(1.2);
  width: 10rem;
}
.seccion1 .informacion .tarjeta:hover .photo {
  width: 85%;
  height: 100%;
  background-size: cover;
}
.seccion1 .informacion .tarjeta:hover .iconos-tarjeta {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 4px;
}
.seccion1 .informacion .tarjeta:hover .iconos-tarjeta .nombre-tarjeta {
  display: inline-block;
  align-items: center;
  margin-top: 10px;
}
.seccion1 .informacion .tarjeta:hover .iconos-tarjeta .contenedor-iconos {
  display: grid;
  grid-template-columns: none;
