.efecto-card{
    animation: cardAnimacion 1.4s;
}

/*------------------animacion tarjetas------------*/
@-webkit-keyframes cardAnimacion {
    0% {
      box-shadow: 0 0 0 0 var( #959595);
    }
}
  
@keyframes cardAnimacion {
    0% {
      box-shadow: 0 0 0 0 var( #959595);
    }
}
  /*-----------------------------------------------*/
  /*------------------animacion texto------------*/
@-webkit-keyframes textAnimacion {
    0% {
      text-shadow:  0 0 0 var(--hoverContorno);
    }
}
  
@keyframes textAnimacion {
    0% {
      text-shadow:  0 0 0 var(--hoverContorno);
    }
}
  /*-----------------------------------------------*/
  
  
  
  /*animacion intermitente text*/
  
@keyframes textanimacionColor {
    0%,
    100%{
      text-shadow:0px 2px 8px rgb(8, 231, 0);
    }
    25% {
      text-shadow:0px 2px 8px rgb(253, 11, 11);
    }
    50% {
      text-shadow:0px 2px 8px rgb(17, 0, 255);
    }
    75% { 
      text-shadow:0px 2px 8px rgb(177, 0, 147);
      
    }
}
@keyframes boxanimacionColor {
    0%,
    100%{
        filter: drop-shadow(0px 2px 8px rgb(8, 231, 0));
      box-shadow:0px 2px 8px rgb(8, 231, 0);
    }
    25% {
      box-shadow:0px 2px 8px rgb(253, 11, 11);
    }
    50% {
      box-shadow:0px 2px 8px rgb(17, 0, 255);
    }
    75% { 
      box-shadow:0px 2px 8px rgb(177, 0, 147);
      
    }
}
@keyframes dropanimacionColor {
    0%,
    100%{
        filter: drop-shadow(10px 10px 20px rgb(8, 231, 0));
        transform: scale(1.1);
      
    }
    25% {
        filter: drop-shadow(0px 2px 20px rgb(253, 11, 11));
        transform: scale(1.2);
    }
    50% {
        filter: drop-shadow(0px 2px 20px rgb(17, 0, 255));
        transform: scale(1.1);
     
    }
    75% { 
        filter: drop-shadow(0px 2px 20px rgb(177, 0, 147));
        transform: scale(1.2);      
    }
}


//----------glow--------------//
@keyframes glowing {
    0% {
     background-position: 0 0;
    }
   
    50% {
     background-position: 400% 0;
    }
   
    100% {
     background-position: 0 0;
    }
}

//---------imagenedgalerias---------
@keyframes cambio{
    0%{margin-left: 0;}
    20%{margin-left: 0;}

    25%{margin-left: -100%;}
    45%{margin-left: -100%;}

    50%{margin-left: -200%;}
    70%{margin-left: -200%;}

    75%{margin-left: -300%;}
    100%{margin-left: -300%;}
}
   
//---Pulso
@keyframes pulso {
  0% {
		opacity: 1;
	}

	50% {
		opacity: 0.2;
	}

	100% {
		opacity: 1;
	}
}
//Slide

@keyframes slideLeft {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
    }
    100% {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
    }
}

//desaparece
@keyframes fadeOut {
  0% {opacity: 1;}
  100% {opacity: 0;}
}

//aparece
@keyframes fadeIn {
  0% {opacity: 0;}
  100% {opacity: 1;}
} 

//rotacion
@keyframes rotacion{
  0%{
    transform: rotate(0);
  }
  100%{
    transform: rotate(360deg);
  }
}

